.api-view {
	// width: 389px;
	height: 264px;
	background: #101626;
	border-radius: 5px;
	// margin-right: 26px;
	color: #f7f8ff;

	.title {
		padding: 29px 0 60px 46px;
		font-family: "pirulen_rg";
	}
	.item-view {
		margin-left: 72px;
		font-size: 11px;
		display: flex;
		align-items: center;
		svg {
			width: 15px;
			height: 15px;
			margin-right: 5px;
		}
		.label-title {
			width: 80px;
		}
	}

	.line {
		width: 289px;
		height: 1px;
		background: #516691;
		margin: 24px auto;
	}
}

.info-view {
	// width: 253px;
	height: 264px;
	background: #101626;
	border-radius: 5px;
	// margin-right: 26px;
	color: #f7f8ff;
	.main-title {
		padding-top: 32px;
		text-align: center;
		margin-bottom: 24px;
		font-family: pirulen_rg;
	}

	.bottom-view {
		display: flex;
		justify-content: space-around;
		.sub-view {
			width: 102px;
			height: 167px;
			background: #060818;
			border-radius: 5px;
			text-align: center;
			display: flex;
			flex-direction: column;
			justify-content: space-around;
			.title {
				font-size: 11px;
				font-weight: 400;
				color: #f7f8ff;
			}
			.num {
				font-size: 35px;
				font-weight: 400;
				color: #f7f8ff;
			}

			.check-btn {
				font-size: 12px;
				color: #079688;
			}
		}
	}
}
.account-view {
	// width: 443px;
	height: 264px;
	background: #101626;
	border-radius: 5px;
	color: #f7f8ff;

	.top-view {
		height: 123px;
		background: linear-gradient(180deg, #009688 0%, #009688 33%, #101626 100%);
		border-radius: 5px;
		display: flex;
		.head-view {
			width: 59px;
			height: 59px;
			border-radius: 29.5px;
			margin-left: 47px;
			/* flex: 1; */
			margin-top: 20px;
		}

		.detail-view {
			margin-right: 35px;
			font-size: 11px;
			margin-top: 20px;
			.name-view {
				display: flex;
				align-items: center;
				font-family: "pirulen_rg";

				svg {
					width: 11px;
					height: 11px;
					margin-right: 3px;
				}
			}
		}
	}

	.bottom-view {
		display: flex;
		margin-top: -30px;
		justify-content: space-around;
		.sub-view {
			width: 180px;
			height: 134px;
			background: #060818;
			border-radius: 5px;
			.title {
				margin: 13px 0 0 15px;
				font-size: 11px;
				font-family: "pirulen_rg";
			}
			.num {
				font-size: 35px;
				font-weight: 400;
				text-align: center;
				margin-top: 10px;
			}
			.balance-view {
				text-align: center;
				margin-top: 25px;
				font-size: 16px;
				span {
					font-size: 11px;
					margin-left: 3px;
				}
			}
		}
	}
}
